* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  display: flex;
  margin-top: 20px;
}
.field {
  display: flex;
  flex-direction: column;
  margin-right: 30px;
}

.value {
  font-weight: bold;
  font-size: 40px;
}

.label {
  text-transform: uppercase;
  font-weight: bold;
}

.input {
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  padding: 10px;
}

button {
  padding: 10px;
  font-size: 20px;
}
